@import url('https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap');

.login_container{
	width: 100vw;
	height: 100vh;
	margin: 120px auto 0px auto;
	background-image: url("./bgc_pattern.svg");
	background-repeat: no-repeat;
}

.login_title{
	font-size: 32px;
	margin-bottom: 40px;
	color: #a0a0a0;
	text-align: center;
}

.login_container p {
	font-size: 16px;
	color: #a0a0a0;
	display: block;
	text-align: center;
}

form{
	width: 480px;
	margin: 10px auto;
	display:flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.text_field{
	margin: auto;
	height: 90px;
}

.input_title{
	font-weight: normal;
	font-size: 14px;
	color: #a0a0a0;
	width: 480px;
	height:20px;
}

.input_hide{
	visibility: hidden;
}

.react-inputs-validation__msg_identifier {
	font-weight: normal;
	font-size: 14px;
	color: #dba23f;
	text-align: right;
}

.errorMsg {
	background-color: #f9f3e8;
	font-weight: normal;
	font-size: 14px;
	border-radius: 5px;
	color: #dba23f;
	text-align: center;
	padding: 8px;
	margin: 20px auto 10px auto;
	width: 480px;
}

i{
	margin: 0px 5px;
}
.input_field{
	font-size: 16px;
	width: 480px;
	height: 48px;
	border: solid 2px #e0e0e0;
	padding: 0px 10px;
	color: #4a4a4a;
	border-radius: 5px;
}

.input_field::placeholder{
	color: #a0a0a0;
}

.input_empt{
	border: solid 2px #d6bb8b;
}

.input_field:focus {
	border: solid 2px #48e3ee;
	outline: none;
}

/* firebaseui */
.login_container .firebaseui-idp-button {
	direction: ltr;
	font-weight: 500;
	height: auto;
	line-height: normal;
	min-height: 48px;
	width: 100%;
}
.login_container .firebaseui-idp-list {
	width: 480px;
	margin: 0px;
}

.login_container .firebaseui-container{
	max-width: 480px;
}
.login_container .firebaseui-text {
	color: rgba(0, 0, 0, 0.87);
	direction: ltr;
	font-size: 16px;
	line-height: 24px;
	text-align: center;
}

.login_container .firebaseui-idp-button {
	direction: ltr;
	font-weight: 500;
	height: auto;
	line-height: normal;
	max-width: 480px;
	min-height: 48px;
	padding: 8px 130px;
	text-align: center;
	width: 100%;
	border-radius: 5px;
}

.login_container .firebaseui-card-content{
	padding: 0;
}

.login_container .mdl-button--raised {
	box-shadow: none;
}

.login_container .firebaseui-idp-google {
	background-color: #ffffff;
	border: 1px solid #e0e0e0;
}
.login_container .firebaseui-idp-google:hover {
	border: 1px solid #aaaaaa;
	transition: all 0.3s ease;
}