.selector_hover_layer {
	width: 100%;
	height: 100vh;
	background-color: rgba(0,0,0,0.7);
	position: fixed;
	top: 0;
	left: 0;
	z-index: 4;
}

.selector_container {
	z-index: 5;
	max-width: 840px;
	height: 520px;
	background-color: #fff;
	border-radius: 10px;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin: auto;
	display:flex;
}

.selector_container::before {
	content:'';
	height: 100%;
	width: 0;
	display: inline-block;
	vertical-align: middle;
}

.btn_close {
	text-decoration: none;
	width: 50px;
	height: 50px;
	line-height: 50px;
	color: #fff;
	background-color: #000;
	text-align: center;
	position: absolute;
	top: 0;
	right: 0;
}

.text {
	padding: 40px;
	text-align: center;
	display: inline-block;
	vertical-align: middle;
	width: calc(100% - 6px);
	box-sizing: border-box;
	background-color:mediumaquamarine;
}

.preview_img_l {
	flex: 45%;
	max-width: 45%;
	height: 100%;
	object-fit: cover;
	border-radius: 10px 0px 0px 10px;
}
.edit_collect{
	display: inline-block;
	flex: 55%;
	max-width: 55%;
	/* width: 55%; */
	padding:50px;
}
@media screen and (max-width: 700px) {
	.preview_img_l{
		display: none;
	}
	.edit_collect{
		flex: 100%;
		max-width: 100%;
	}
}
.selector_title {
	font-size: 24px;
	width: 100%;
	height: 30px;
	text-align: center;
	display: inline-block;
	color: #4a4a4a;
	font-weight: bold;
	margin: 0px auto 30px auto;
}
.add_button {
	width: 100%;
	height: 50px;
	font-size: 16px;
	background-color: #f5f5f5;
	color: #a0a0a0;
	border: 2px dashed #d1d1d1;
	border-radius: 5px;
	cursor: pointer;
} 

.add_button:hover {
	background-color: #eeeeee;
}

.add_input {
	max-width: 304px;
	height: 50px;
	border-radius: 5px;
	border:none;
	background-color: #f3f3f3;
	padding: 0px 10px;
	color: #4a4a4a;
	font-size: 16px;
}

.create_wrapper {
	display: flex;
}

.submit_add{
	width: 50px;
	height: 50px;
	background-color: #fff;
	border: none;
	border-radius: 5px;
	background-color: #f3f3f3;
	color: #a0a0a0;
	cursor: pointer;
	font-size: 22px;
	margin-left: auto;
}

.submit_add:hover {
	background-color: #48e3ee;
	color: #fff;
}

.all_collection {
	padding: 30px 0;
}

.item_group_s {
	/* background-color: #f3f3f3; */
	width: 100%;
	height: 254px;
	margin: 10px 0;
	overflow: scroll;
}

.item {
	position: relative;
	cursor: pointer;
	font-weight: bold;
}
.item_s {
	color: #fff;
	line-height: 80px;
	text-align: center;
	margin-bottom: 10px;
}

.item:hover {
	background-color: rgba(0,0,0,0.3) ;
	border-radius: 5px;
}

.preview_img_s {
	position: absolute;
	border-radius: 5px;
	top:0;
	left:0;
	width: 100%;
	height: 80px;
	object-fit: cover;
	z-index: -2;
}

.item:before {
	content:'';
	border-radius: 5px;
	position: absolute;
	top:0;
	left:0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.2) ;
	z-index: -1;
}